import React, { useState} from 'react'
import {RiMenu3Line, RiCloseLine} from 'react-icons/ri';
import './navbar.css';
import { NavLink } from "react-router-dom";


const Menu = () => (
  <>
  <p className='highlight'><a href="#home" >Home</a></p>
  <p className='highlight'><a href="#whatZach" >About Me</a></p>
  <p className='highlight'><a href="#features" >Some Cool Stuff</a></p>
  <p className='highlight'><a href="#Contact">Contact Info</a></p>
  </>
)

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);
  return (
    <div className="main__navbar">
      <div className="main__navbar-links">
        <div className="main__navbar-links_container">
          <Menu />
        </div>
      </div>
      <div className="main__navbar-menu">
        {toggleMenu
          ? <RiCloseLine color="#fff" size={27} onClick={() => setToggleMenu(false)} />
          : <RiMenu3Line color="#fff" size={27} onClick={() => setToggleMenu(true)} />
          }
          {toggleMenu && (
            <div className="main__navbar-menu_container scale-up-center">
              <div className ="main__navbar-menu_container-links"> 
                <Menu />
              </div>
            </div>
          )}
      </div>
    </div>
  )
}


export default Navbar