import React from 'react'
import './header.css'
import {FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';


const Header = () => {
  return (
    <div className="main__header section__padding" id="home">
      <div className="main__header-content">
        <h1 className="gradient__text" >
            <span>Z</span><span>a</span><span>c</span><span>h</span> <span>S</span><span>i</span><span>m</span><span>m</span><span>o</span><span>n</span><span>s</span> </h1>      
            <div className="main__header-icons">   
              <a href="https://www.linkedin.com/in/zachary-simmons-5308311b8/">
                <FontAwesomeIcon icon={faLinkedin} size="5x" color="#222" className='highlight'></FontAwesomeIcon>
              </a>
              <a href="https://github.com/zsim314">
                <FontAwesomeIcon icon = {faGithub} size ="5x" color="#222" className='highlight'></FontAwesomeIcon>
              </a>
          </div>  
      </div>
    </div>
  )
}

export default Header