import React from 'react'
import './whatzach.css'


const Whatzach = () => {
  return (
    <div className="main__whatzach section__margin" id="whatZach">
      <div className='main__whatzach-whozach'>
          <h1>Who Am I?</h1>
      </div>
      <div className="main__whatzach-content">
        <p>Hello! I am a 19 year old Undergraduate Computer Science student currently studying at University of Texas at Dallas. I have been interested in coding since I was young, and started actually 
          learning programming as a freshman in highschool.
        </p>
      </div>
    </div>
  )
}

export default Whatzach